:root {
  --header-color: #000000;
  --hover-color: #f5f5f5;
  --active-button: #04aa6d;
  --hover-font-color: #696969;
  --font-color: #d3d3d3;
  --page-font: "League Gothic", sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
}

header {
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
}

header div h1 {
  font-size: 90px;
  margin-top: 0;
  margin-bottom: 0;
  font-family: var(--page-font);
  padding-left: 10px;
  letter-spacing: 1px;
  background-color: white;
}

header ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
  overflow: hidden;
  background-color: var(--header-color);
  margin-top: 0px;
  padding-left: 0;
}

header div ul li a {
  display: block;
  color: var(--font-color);
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-family: var(--page-font);
  font-size: 30px;
  letter-spacing: 1px;
  width: 33vw;
}

header div ul li a:hover {
  background-color: var(--hover-color);
  color: var(--hover-font-color);
}

.section-heading {
  text-align: center;
  font-family: var(--page-font);
  font-size: 40px;
  letter-spacing: 2px;
}

.information-container {
  display: flex;
  justify-content: space-around;
  font-family: var(--page-font);
}

.information-container img {
  width: 400px;
  border-radius: 20px;
}

.box-content {
  text-align: center;
  width: 60%;
}

.box-content h3 {
  font-size: 30px;
}

.box-content p {
  font-size: 25px;
}

.aboutme-container {
  padding-top: 50px;
  height: 500px;
}

.contact-container {
  text-align: center;
  font-family: var(--page-font);
  font-size: 25px;
}

.contact-container a {
  font-family: var(--page-font);
  font-size: 25px;
}

/* See my work stuff */
#seemywork {
  height: 600px;
}

.showcase {
  text-align: center;
}

.examples-row {
  display: flex;
  justify-content: space-around;
}

.workexample img {
  border-radius: 25px;
}

.showcase a img {
  width: 600px;
}

.examples-row a img {
  width: 300px;
}










@media screen and (max-width: 768px) {

  header div h1 {
    font-size: 35px;
  }

  .nav-btn {
    display: flex;
  }

  header div ul {
    flex-direction: column;
    width: 100%;
    align-items: center;
  }

  header div ul li a {
    font-size: 25px;
  }

  .section-heading {
    text-align: center;
    font-family: var(--page-font);
    font-size: 30px;
    letter-spacing: 2px;
  }

  /* aboutme */
  .aboutme-container {
    height: 700px;
    text-align: center;
  }

  .information-container {
    flex-direction: column;
    align-items: center;
  }

  .information-container img {
    width: 200px;
  }

  /* seemywork */
  #seemywork {
    display: flex;
    flex-direction: column;
    height: 700px;
  }

  .showcase a img {
    width: 350px;
  }

  .examples-row {
    flex-direction: column;
    align-items: center;
  }

  .examples-row a img {
    border-radius: 99999px;
    width: 200px;
  }

  .showcase div a h4 {
    text-decoration: none;
    color: pink;
  }
}